.column {
  display: inline-block;
  text-align: center;
}
.frame {
  text-align: center;
  cursor: pointer;
  /* perspective: 500px; */
}
.frame img {
  width: 100%;
  height: 400px;
}
.frame .details {
  width: 70%;
  height: 80%;
  padding: 5% 8%;
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotateY(90deg);
  transform-origin: 50%;
  background: rgba(255, 255, 255, 0.9);
  opacity: 0;
  transition: all 0.4s ease-in;
}
.frame:hover .details {
  transform: translate(-50%, -50%) rotateY(0deg);
  opacity: 1;
}



